@font-face {
    font-family: 'Inter';
    font-weight: 900;
    font-style: normal;
    font-display: swap;
    src: local('Inter'), url(./fonts/Inter-Black.ttf) format('ttf'),
    url(./fonts/Inter-Black.woff) format('woff'),
    url(./fonts/Inter-Black.woff2) format('woff2');
}

